import React, {Fragment} from 'react'
import '../../styles/words.css'


export default function City({city}){
    return(
        <Fragment>
        <div className='heading'><h1>{city.city}</h1></div>
        <div className="couple">
        <div className="tableCities">
            <ul type="none">
                    <li> <span>admin: </span>{city.admin}</li>
                    <li><span>proper population: </span>{city.population_proper}</li>
                    <li><span>population: </span> {city.population}</li>
                    <li><span>lat: </span> {city.lat}</li>
                    <li><span>lng: </span> {city.lng}</li>

                </ul>
        </div>
        </div>
        </Fragment>
    )
}