function App() {

  return (
    <div className="App">
      <Router>

        <Switch>
          <Route exact path="/erfahren/">
            <Redirect to='/erfahren/ua'/>
          </Route>

          <Route  path="/erfahren/ua">
            <Pages/>
          </Route>

          <Route  path="/erfahren/de">
            <Pages/>
          </Route>

          <Route  path="/erfahren/en">
            <Pages/>
          </Route>

          <Route  path="/erfahren/cities">
            <Redirect to='/erfahren/ua/cities'/>
          </Route>

          <Route  path="/erfahren/words">
            <Redirect to='/erfahren/ua/words'/>
          </Route>

          <Route  path="/erfahren/api">
            <Redirect to='/erfahren/ua/api'/>
          </Route>

          <Route>
              <Header lang={'/ua'} />
              <FourOFour />
          </Route>
        </Switch>

      </Router>
    </div>
  );
}


function Pages(){
  let { path, url } = useRouteMatch();
  return(
    <Fragment>

      <Header lang={url.slice(0, 3)} />
      <Switch>
          <Route exact path={path}>
            <Main lang={path.slice(1,3)} />
          </Route>
          <Route path={`${path}/:pageId`}>
            <Page lang={path} />
          </Route>
          <Route>
            <FourOFour lang={path}/>
          </Route>

      </Switch>
    </Fragment>
  )

}


function Page(props){
  let { pageId } = useParams();
  
  let comp;
  switch(pageId){
    case 'words':
      comp = <Words lang={props.lang.slice(1,3)}/>
      break;
    case 'cities':
      comp = <Cities lang={props.lang.slice(1,3)}/>
      break;
    case 'api':
      comp = <Api lang={props.lang.slice(1,3)}/>
      break;
    default :
      comp = <FourOFour />

  }

  return (
    comp
  );
}



function Main(porps){
  return(
    <div className="main">
        <div className="middleText">
          <h1>Deutschland erfahren!</h1>
            <p>{text.text[porps.lang]}</p>
        </div>
    </div>
  )
}


