import React, {useState, useEffect} from 'react'
import loader from '../../assets/oval.svg'
import '../../styles/words.css'
import City from './City'

const axios = require('axios');

const text ={
    text:{
        ua: 'місто з\'явиться тут',
        de: 'die Stadt wird here entstehen',
        en: 'City will appear here'
    },
    placeholder:{
        ua: 'введіть назву міта',
        de: 'geben Sie die Name der Stadt ein',
        en: 'type name of the city'
    }
}


export default function Cities(props){
    const [city, setCity] = useState('')
    const [data, setData] = useState('')
    const [error, setError] = useState(false)
    const [loading, setLoading] = useState(true)
    const [usedData, setUsedData]= useState({})
    const [typedText, setTypedText]= useState('')

    const handleForm = (e)=>
    {
        setError(false)
        e.preventDefault()
        setCity(e.target.city.value.trim().toLowerCase())
        e.target.city.value =''
    }


    useEffect(()=>{
        async function getCities()
        {
            try{
                let res = await axios(
                    `https://verbs-server.herokuapp.com/cities/${city}`);

                let cities = await res.data
                

                setData(cities)
                setLoading(false)

                let obj ={}
                obj[city.toLowerCase()] = cities

                setUsedData(()=> Object.assign(usedData, obj))

                setError(false)

                }catch(err){

                    setError(err)
                    setLoading(false)

                }

            }

            if ( !Object.keys(usedData).includes(city) )
            {
                if (city !== ''){
                
                setLoading(true)
                getCities()
                }
            }else
            {
                setData(usedData[city])
            }

    }, [city, usedData])
    
    return (
        <div className="main">
        <div className="formWrapper">
            <div className="typedWord" >
                <h1 className='typedWordText'>{typedText}</h1>
            </div>
            <div className="wordPlatform"  >
                <form  onSubmit={(e)=> handleForm(e)} >
                    <input type="text" name='city' onChange={(e)=> setTypedText(e.currentTarget.value) }  placeholder={text.placeholder[props.lang]} autoComplete='off'/>
                </form>
                <div className="info" id='info'>
                {city ? 
                    loading? 
                        <img src={loader} alt='loader'/> 
                        : 
                        !error ?
                            <City city={data}/>
                            : 
                            <h1 className='yourWord'>Not Found</h1> 
                    :
                    <h1 className='yourWord'>{text.text[props.lang]}</h1>}
                </div>
            </div>
        </div>
    </div>
    )
}