import React from 'react'
import '../styles/api.css'
import Highlight from 'react-highlight.js' 


const code = 
`
{
    "verb":
        "{
        "Infinitive": "kommen",
        "Präsens_ich": "komme",
        "Präsens_du": "kommst",
        "Präsens_er, sie, es": "kommt",
        "Präteritum_ich": "kam",
        "Partizip II": "gekommen",
        "Konjunktiv II_ich": "käme",
        "Imperativ Singular": "komm",
        "Imperativ Plural": "kommt",
        "Hilfsverb": "sein"
        }",
    "noun":
        "{
            "DAT":
                {"SIN":"Kommen"},
            "GEN":
                {"SIN": "Kommens"},
            "AKK":
                {"SIN":"Kommen"},
            "NOM":
                {"SIN":"Kommen"W},
            "G":"N"
        }"
}

`

const code2 =
`
{
    "verb":false,
    "noun":
        {"DAT":
            {
            "SIN":"Gurke",
            "PLU":"Gurken"
            },
        "GEN":
            {
            "SIN":"Gurke",
            "PLU":"Gurken"
            },
        "AKK":
            {
            "SIN":"Gurke",
            "PLU":"Gurken"
            },
        "NOM":
            {
            "SIN":"Gurke","
            PLU":"Gurken"
            },
        "G":"F"
        }
}

`

const code3 = 
`
{
    "city":"Berlin",
    "admin":"Berlin",
    "country":"Germany",
    "population_proper":"3094014",
    "iso2":"DE",
    "capital":"primary",
    "lat":"52.516667",
    "lng":"13.4",
    "population":"3406000"
}

`

export default ()=>{
    return(
        <div className="main">
            <div className="middleTextAPI">
                <h1>API</h1>

            <div className="wordsApi">
                {/* <hr/> */}
                <h1>Слова api</h1>
                <p>для отримання інформації про німецькі слова (дієслова та іменники) з сервера потрібно відправити GET запит на адресу <span className='ApiLink'>https://verbs-server.herokuapp.com/words/слово</span></p>
                <br/>
                <p>приклад: <span className='ApiLink'>https://verbs-server.herokuapp.com/words/kommen</span></p>
                <p>запит повертає інформацію про про знайдені дієслова та іменники, інколи одне слово може бути одночасно і іменником і дієсловом, як у прикладі нижче, проте так буває не завжди, якщо знайдено тільки іменник 
                або тільки дієслово, то не знайдений параметр буде зазначено як false, якщо не знайдено ні іменника ні діжслова сервер повертає стату 404</p>
                <br/>
                <div className="example">
                    <Highlight language={'json'}>
                        {
                            code        
                        }
                    </Highlight>
                </div>
                <br/>
                <p>у цьому прикладі сервер повертає лише один із параметрів - іменник</p>
                <p>приклад: <span className='ApiLink'>https://verbs-server.herokuapp.com/words/gurke</span></p>
                <br/>

                <div className="example">
                    <Highlight language={'json'}>
                        {
                            code2        
                        }
                    </Highlight>
                </div>
            <br/>
                <hr/>
            </div>
            <div className="citiesApi">
                <h1>Міста api</h1>
                <p>для отримання інформації про міста потріюно відправити GET запит на <span className='ApiLink'>https://verbs-server.herokuapp.com/cities/назва міста</span> </p>
                <br/>
                <p>приклад: <span className='ApiLink'>https://verbs-server.herokuapp.com/cities/berlin</span></p>
                <p>запит повертає інформацію про це місто, якщо такого міста не знайдено сервер повертає статус 404</p>
                <br/>
                
                <div className="example">
                    <Highlight language={'json'}>
                        {
                            code3  
                        }
                    </Highlight>
                </div>
                <br/>
                <br/>
                <br/>


            </div>
            </div> 
        </div>
    )
}