import React, {useState, useEffect} from 'react'
import '../../styles/words.css'
import WordsListing from './WordsListing'
import loader from '../../assets/oval.svg'

const axios = require('axios');


const text ={
    text:{
        ua: 'слово з\'явиться тут',
        de: 'das Wort wird here entstehen',
        en: 'Word will appear here'
    },
    placeholder:{
        ua: 'введіть ваше слово',
        de: 'geben Sie ihr Wort ein',
        en: 'type your word'
    }
}

export default function Words(props){
    const [loading, setLoading]= useState(true)
    const [data, setData] = useState('')
    const [word, setWord] = useState('')
    const [typedText, setTypedText]= useState('')
    const [usedData, setUsedData]= useState({})
    const [error, setError]= useState(false)
    // const [plaformStatus, setPlaformStatus]= useState(false)
    // const [scrollPos, setScrollPos]= useState(0)



    const handleForm = (e)=>
    {
        e.preventDefault()
        setWord(e.target.word.value.trim().toLowerCase())
        e.target.word.value =''
    }


    useEffect(() =>{
        async function getwords()
        {
        try{
        let res = await axios(
            `https://verbs-server.herokuapp.com/words/${word}`);

        let resD = await res.data
        console.log(res.data)
        setData({noun: resD.noun, verb: resD.verb})

        setLoading(false)

        let obj ={}
        obj[word.toLowerCase()] ={noun: resD.noun, verb: resD.verb}
        setUsedData(()=> Object.assign(usedData, obj))
        setError(false)

        }catch(err){
            
            setError(err)
            setLoading(false)

        }
        }
        if ( !Object.keys(usedData).includes(word) )
        {
            if (word !== ''){
            setLoading(true)
            getwords()
            
            }
        }else{
            setData(usedData[word])
        }

      },[word, usedData]);

    return(
        <div className="main">
            <div className="formWrapper">
                <div className="typedWord" >
                    <h1 className='typedWordText'>{typedText}</h1>
                </div>
                <div className="wordPlatform"  >
                    <form  onSubmit={(e)=> handleForm(e)} >
                        <input type="text" name='word' onChange={(e)=> setTypedText(e.currentTarget.value) }  placeholder={text.placeholder[props.lang]} autoComplete='off'/>
                    </form>
                    <div className="info" id='info'>
                        {word ? 
                            loading? 
                                <img src={loader} alt='loader'/> 
                                : 
                                !error?
                                    <WordsListing lang={props.lang} word={data}/>
                                    : 
                                    <h1 className='yourWord'>Not Found</h1> 
                            :
                            <h1 className='yourWord'>{text.text[props.lang]}</h1>}
                    </div>
                </div>
            </div>
        </div>
    )
}


